.KycLayout {
  width: 100%;
  height: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
  grid-template-rows: 60px 1fr auto;
}

.KycTitless {
  position: initial !important;
}

.inputLabelsCont.loader,
.InputContainerT.loader,
.KycTitless.loader,
.inputContainer3.loader p {
  background: #cacaca;
  border-radius: 6px;
}

.InputContainerT.loader {
  width: 65%;
  height: 100%;
}

.inputLabelsCont.loader {
  width: 45%;
  height: 50%;
}

.inputContainer3.loader p {
  height: 20px;
  width: 120px;
  position: relative;
  left: 15px;
}

.KycTitless.loader {
  height: 35px;
