}

.KycLayoutBasicWin h1 {
  text-align: center;
}

.KycLayoutBasicWin {
  width: 100%;
  height: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-rows: 10% 40% 15% 15% 20%;
