}

.contexpandibleKycPanel {
  height: 100%;
  width: 100%;
  max-width: 630px;
  opacity: 0;
}

.contexpandibleKycPanel.openSec {
  animation-name: openSec;
  animation-duration: 0.2s;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

@keyframes openSec {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

.expandibleKycPanel {
  width: 100%;
  transition: 0.3s;
  overflow: hidden;
  display: grid;
  justify-items: center;
}

.KycParra1 {
