.contButtonUpload {
  max-width: 200px;
  position: relative;
  cursor: pointer;
}

.contButtonUpload input {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}

.controlContainers {
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
}

.KycDashBoard {
  width: 100%;
  height: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative !important;
}

.imgDashContainer {
  width: 100%;
  height: 90% !important;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 1fr 120px 60px;
  grid-row-gap: 7px;
