}

.imgDashContainer > p {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
  color: #636363;
  margin: 0 0 30px 0;
}

.imgDashCarousel {
  display: grid;
  /*grid-template-columns: repeat(3, 1fr);*/
  align-items: center;
  column-gap: 3px;
  width: 100%;
  /*max-width: 400px;*/
  justify-self: center;
}

.imgDashStep i {
  position: absolute;
  color: green;
  font-size: 25px;
  right: 3px;
  top: 3px;
}

.kycPropgressed {
  height: 100%;
  width: 20%;
  transition: 0.3s;
  background: linear-gradient(to right, #129a8e, #37ed7d);
}

.topThree {
  margin: 0 !important;
  font-size: 30px !important;
}

.parrafeins {
  font-weight: normal !important;
}

.KycprogressBar {
  height: 7px;
  background: #80808026;
  width: 100%;
  position: absolute;
  top: 0;
}

.imgDashStep {
  -webkit-box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0);
  -moz-box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0);
  box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0);
  height: 90px;
  transition: 0.25s;
  cursor: pointer;
  border: 1px solid #808080a1;
  transform: scale(0.9);
  display: grid;
  align-items: center;
  justify-items: center;
  border-bottom: 2px solid transparent;
  opacity: 0.3;
  position: relative;
  /*border: 1px solid gray;*/
}

.imgDashStep:hover {
  -webkit-box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0.2);
  transform: scale(1);
  border: 1px solid #8080804a;
  opacity: 1;
  /*border-bottom: 2px solid #129A8E;*/
}

.imgDashStep.active {
  -webkit-box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 39px 0px rgba(0, 0, 0, 0.2);
  transform: scale(1);
  border: 1px solid #8080804a;
  opacity: 1;
  border-bottom: 2px solid #129a8e;
}

.imgDashContainerD {
  width: 100%;
  height: 100%;
  /*background: black;*/
  transition: 0.45s;
  transform: scale(1);
  cursor: pointer;
  z-index: 1;
  display: grid;
  align-items: center;
  justify-items: center;
  perspective: 350px;
}

.imgDashSon:hover {
  transform: scale(1.1);
  z-index: 2;
}

.imgDashSon {
  width: 70% !important;
  height: 80% !important;
  display: grid;
  align-items: center;
  justify-items: center;
  transform: scale(1);
  transition: 0.45s;
  position: relative;
  perspective: 850px;
}

.imgDashSon img {
  position: absolute;
  transition: 0.4s;
  /*transition: .01s;*/
}

.imgDCAnim {
  animation-name: imgDCAnim;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes imgDCAnim {
  0% {
    transform: scale(1);
  }

  30% {
    transform: scale(0.8);
  }

  60% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1);
  }
}

/*.backImg{
  transform: rotateY(0deg) !important;
}*/

/*.frontImgs{
  backface-visibility: hidden;
}*/

.frontImg {
  animation-name: imgDAnimSon;
  backface-visibility: hidden;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes imgDAnimSon {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(-180deg);
  }
}
.backInit {
  transform: rotateY(180deg);
  backface-visibility: hidden;
}

.imgDashItem.backImg {
  animation-name: imgDAnimSon22;
  backface-visibility: hidden;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

.imgDashItem.pasaporte {
  transform: scale(0.7) !important;
}

@keyframes imgDAnimSon22 {
  0% {
    transform: rotateY(180deg);
  }
  100% {
    transform: rotateY(0deg) !important;
  }
}

.continueKyc {
  font-size: 19px;
