}

/*KYC BASIC CONTAINER STYLES*/

.itemListCountry {
  width: 100%;
  height: 50px;
  border: 1px solid gray;
  border-radius: 6px;
}

.contCountryList {
  width: 100%;
  height: 65vh;
  overflow: scroll;
  overflow-x: hidden;
  display: grid;
  grid-template-rows: repeat(auto-fill, 70px);
  grid-row-gap: 10px;
}

.contCountryList {
  padding: 0 10px;
  cursor: pointer;
}

.contCountryList::-webkit-scrollbar {
  width: 0px !important;
  background: transparent;
}

.contCountryList::-webkit-scrollbar-thumb {
  background: transparent !important;
}

/*KYC BASIC CONTAINER STYLES ITEM LIST*/

.ItemListKycBasic {
  width: calc(100% - 40px);
  height: 70px;
  border-radius: 6px;
  padding: 0 20px;
  display: grid;
  align-items: center;
  transition: 0.1s;
}

.ItemListKycBasic:hover {
  background: #f3f4f4;
}

.ItemListKycBasic p {
  margin: 0;
  display: flex;
  align-items: center;
  color: #50667a;
  text-transform: capitalize;
}

.ItemListKycBasic img {
  border-radius: 6px;
  margin-right: 10px;
}

.ItemListKycBasic.active p {
  color: #38ef7d !important;
}

.ItemListKycBasic.active {
  background: linear-gradient(to bottom right, #2b3742, #151b20);
  border: 1px solid #38ef7d;
}

