  margin-top: 25px;
}

.KycFinancialComponent p {
  color: gray;
  width: 100%;
  max-width: 750px;
  text-align: center;
}

.loaderFinancial {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  background: #ffffffc9;
  display: grid;
  align-items: center;
  justify-items: center;
}

.loaderFinancial_cont {
  position: relative;
  transform: scale(0.9);
}

.panelImgFinancial {
  position: relative;
  background: #f5f5f5;
  width: 100%;
  max-width: 750px;
  width: 100%;
  height: 100%;
  border-radius: 7px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr auto;
  -webkit-box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.28);
  -moz-box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.28);
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.28);
}

.controlsFinancial {
