  max-width: 750px;
  background: red;
  width: 100%;
  height: 100%;
}

.FinancialUploadImg {
  height: calc(100% - 40px);
  padding: 20px;
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
}

.KycprogressBar.financial {
  bottom: 0 !important;
  top: auto !important;
}

.FinancialUploaded {
  transition: 0.3s;
  background: linear-gradient(to bottom, #2786c5, #0062a2);
  overflow: hidden;
  display: grid;
  grid-template-rows: repeat(auto-fill, 70px);
  grid-row-gap: 10px;
}

.loaderImgComponent {
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  border: 2px dashed #3e87d9;
  border-radius: 6px;
  max-width: 450px;
  max-height: 290px;
  cursor: pointer;
  background: #f1f9ff;
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
  padding: 10px;
  transform: scale(0.9);
  transition: 0.3s;
}

.textFinancial {
  color: #3e87d9;
  margin: 10px 0;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-row-gap: 10px;
  align-items: center;
  text-align: center;
  justify-items: center;
  width: 100%;
  max-width: 400px;
  /*font-size: 14px;*/
}

.payloadFinancial {
  display: grid;
  justify-items: center;
  position: relative;
}

#uploadIMGF {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 1;
}

.textFinancial .iconSty {
  animation-name: touchRUT;
  animation-duration: 1s;
  /*animation-fill-mode: forwards;*/
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

@keyframes touchRUT {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

.item_loaded {
